
.items input {
	border: none;
    padding: 4px 5px;
    margin: 2px 4px;
    border-radius: 8px;
    background-image: linear-gradient(to right, #afe8df 0%, #8498db 51%, #9EEFE1 100%); 
    font-size: 14px;
    color: white;
    box-shadow: 0 16px 32px 2px rgba(0, 40, 120, .35);
    transition: .5s;
}

.items button {
    padding: 3.5px ;
	border: none;
    border-radius: 15px 55px 55px 15px;
    background-image: linear-gradient(45deg, #021c7a 10%, #d06cd9e1 50%, #0f8b7d 100%);
    background-position: 100% 1;
    background-size: 200% 200%;
    font-size: 14px;
    color: white;
    box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
    transition: .5s;
  }

.items input:hover {
  transform: scale(1.01);
  
}

.items button:hover {
  transform: scale(1.02);
  
}